<template>
  <div class="dataChart">
    <Nav :is-show-back-arrow="false">疲劳报表</Nav>
    <div class="content">
      <el-card class="box-card history">
        <div class="history-title title">疲劳历史</div>
        <div id="history-chart-1" class="history-chart"></div>
      </el-card>
      <el-card class="box-card history">
        <div class="history-title title">今日分析</div>
        <div id="history-chart-2" class="history-chart"></div>
      </el-card>
    </div>
  </div>
</template>

<script>
import Nav from "../../components/Nav";
import url from "../../api/url";
export default {
  name: "DataChart",
  components: {
    Nav,
  },
  data() {
    return {};
  },
  methods: {
    async getResult(option) {
      return new Promise(async (resolve, reject) => {
        option.xAxis[0].data.forEach((item, index, arr) => {
          arr[index] = this.getDay(-(6 - index)).day;
        });
        for (let index = 0; index < 7; index++) {
          let result = await this.$axios.get(url.getResult.path, {
            params: {
              start: this.getDay(-(6 - index)).seconds,
              end: this.getDay(-(5 - index)).seconds,
            },
          });
          option.series[0].data[6 - index] = result.count;
        }
        console.log(111);
        resolve(option);
      });
    },
    getResult1(option) {
      return option;
    },
    getDay(day) {
      //Date()返回当日的日期和时间。
      var days = new Date();
      //getTime()返回 1970 年 1 月 1 日至今的毫秒数。
      var gettimes = days.getTime() + 1000 * 60 * 60 * 24 * day;
      //setTime()以毫秒设置 Date 对象。
      days.setTime(gettimes);
      var year = days.getFullYear();
      var month = days.getMonth() + 1;
      if (month < 10) {
        month = month;
      }
      var today = days.getDate();
      if (today < 10) {
        today = today;
      }
      return {
        day: month + "/" + today,
        seconds: gettimes,
      };
    },
  },
  mounted() {
    this.$init(this.getResult, "baseInfoOption1", "history-chart-1");
    this.$init(this.getResult1, "deviceState", "history-chart-2");
  },
};
</script>

<style scoped lang="less">
.dataChart {
  width: 100vw;
  height: 100vh;
  overflow: scroll;
  background-color: rgba(234, 234, 234, 0.6);
  box-sizing: border-box;
  letter-spacing: 1px;
  .content {
    padding: 0 20px;
    .history {
      .title {
        text-align: center;
        font-size: 17px;
        font-weight: 450;
      }
    }
  }
}
.box-card {
  width: 100%;
  min-height: 200px;
  margin: 20px auto;
  border-radius: 12px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}
.history-chart {
  width: 300px;
  min-height: 150px;
}
#history-chart-2 {
  margin-top: 20px;
  min-height: 300px;
}
</style>